import React, { useContext } from "react";
import { GlobalContext } from "../../context/GlobalState";
import clsx from "clsx";
import {
  FormGroup,
  FormControlLabel,
  Checkbox,
  Button,
  Paper,
  Typography,
  useMediaQuery,
} from "@material-ui/core";
import { makeStyles, useTheme } from "@material-ui/core/styles";
import { capitalizeFirstLetter } from "../../utils/capitalizeFirstLetter";

const useStyles = makeStyles((theme) => ({
  root: {
    flexGrow: 1,
  },
  lineItem: {
    display: "flex",
    height: "35px",
  },
  lineItemText: {
    [theme.breakpoints.down("lg")]: {
      padding: "6px 0",
    },
    [theme.breakpoints.down("xs")]: {
      fontSize: "0.75rem",
    },
  },
  icon: {
    borderRadius: 3,
    width: 16,
    height: 16,
    boxShadow:
      "inset 0 0 0 1px rgba(16,22,26,.2), inset 0 -1px 0 rgba(16,22,26,.1)",
    backgroundColor: "#f5f8fa",
    backgroundImage:
      "linear-gradient(180deg,hsla(0,0%,100%,.8),hsla(0,0%,100%,0))",
    "$root.Mui-focusVisible &": {
      outline: "2px auto rgba(19,124,189,.6)",
      outlineOffset: 2,
    },
    "input:hover ~ &": {
      backgroundColor: "#ebf1f5",
    },
    "input:disabled ~ &": {
      boxShadow: "none",
      background: "rgba(206,217,224,.5)",
    },
  },
  checkedIcon: {
    backgroundColor: "#137cbd",
    backgroundImage:
      "linear-gradient(180deg,hsla(0,0%,100%,.1),hsla(0,0%,100%,0))",
    "&:before": {
      display: "block",
      width: 16,
      height: 16,
      backgroundImage:
        "url(\"data:image/svg+xml;charset=utf-8,%3Csvg xmlns='http://www.w3.org/2000/svg' viewBox='0 0 16 16'%3E%3Cpath" +
        " fill-rule='evenodd' clip-rule='evenodd' d='M12 5c-.28 0-.53.11-.71.29L7 9.59l-2.29-2.3a1.003 " +
        "1.003 0 00-1.42 1.42l3 3c.18.18.43.29.71.29s.53-.11.71-.29l5-5A1.003 1.003 0 0012 5z' fill='%23fff'/%3E%3C/svg%3E\")",
      content: '""',
    },
    "input:hover ~ &": {
      backgroundColor: "#106ba3",
    },
  },
}));

const ShoppingListItem = ({ shoppingItems, tag }) => {
  const classes = useStyles();
  const theme = useTheme();
  const isBigScreen = useMediaQuery(theme.breakpoints.up("md"));

  // Items from Global Context
  const { markShoppingItemCompleted } = useContext(GlobalContext);

  return (
    <>
      <FormGroup>
        {shoppingItems.map((item) => (
          <Paper
            elevation={0}
            key={item.id}
            className={classes.lineItem}
            style={
              item.isCompleted
                ? {
                    textDecoration: "line-through",
                    color: "gray",
                  }
                : null
            }
          >
            <FormControlLabel
              control={
                <Checkbox
                  checked={item.isCompleted}
                  onChange={() => markShoppingItemCompleted(item.id)}
                  name={item.name}
                  checkedIcon={
                    <span className={clsx(classes.icon, classes.checkedIcon)} />
                  }
                  icon={<span className={classes.icon} />}
                />
              }
              label={capitalizeFirstLetter(item.name)}
              style={{ marginRight: "5px" }}
            />
            <Typography className={classes.lineItemText}>
              {" - " + item.quantity + " " + item.sizeOrUnit}
            </Typography>

            {isBigScreen && tag && item.category ? (
              <Button color="primary" size="small">
                {item.category}
              </Button>
            ) : null}
          </Paper>
        ))}
      </FormGroup>
    </>
  );
};

export default ShoppingListItem;
